@charset "UTF-8";
.menu-bar ul {
  list-style: none;
  margin: 0;
}
.menu-bar details {
  padding: 10px;
}
.menu-bar summary {
  padding: 5px 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  border-bottom: 1px solid #f5f5f5;
}
.menu-bar summary .svg-icon {
    margin-right: 5px;
}
.widget-list {
  padding: 0;
}
.menu-item {
  text-align: center;
  cursor: pointer;
  padding: 15px 0 8px;
  transition: all 0.2s;
  position: relative;
}
.menu-item:hover {
    transform: scale(1.2);
}
.menu-item .svg-icon {
    font-size: 24px;
}
.menu-item .menu-caption {
    display: block;
    font-size: 12px;
    width: 100%;
}
.layer-list {
  padding: 10px;
}
.layer-list li {
    text-align: left;
    cursor: pointer;
    white-space: nowrap;
    line-height: 24px;
    padding-left: 5px;
}
.layer-list li:hover {
      background: #f5f5f5;
}
.layer-list li::before {
      content: '\203A   ';
}
.layer-list li.layer-active {
      color: #fff;
      background: #000;
}
